<template>
  <b-container class="cntnr" v-show="this.questions.length>=25">
    <b-row class="text-light lead">
      <b-col cols="12" class="text-center h1 mt-2">
        Sual:
      </b-col>
      <b-col cols="12" class="text-center h4 mt-3 question"
      >
        {{this.current_question}}
      </b-col>
    </b-row>

    <b-row>
      <b-col cols="12" class="mt-5">
        <b-button :disabled="buttons_disabled" variant="light" id="a" block @click="state($event)" class="my-buttons">
          {{this.variants.a}}
        </b-button>
      </b-col>

      <b-col cols="12" class="my-1">
        <b-button :disabled="buttons_disabled" variant="light" id="b" block @click="state($event)" class="my-buttons">
          {{this.variants.b}}
        </b-button>
      </b-col>

      <b-col cols="12" class="mb-1">
        <b-button :disabled="buttons_disabled" variant="light" id="c" block @click="state($event)" class="my-buttons">
          {{this.variants.c}}
        </b-button>
      </b-col>

      <b-col cols="12">
        <b-button :disabled="buttons_disabled" variant="light" id="d" block @click="state($event)" class="my-buttons">
          {{this.variants.d}}
        </b-button>
      </b-col>

      <b-col cols="12">
        <b-button :disabled="buttons_disabled" variant="light" id="e" block @click="state" :style="state"
                  class="mt-1 my-buttons">{{this.variants.e}}
        </b-button>
      </b-col>

      <b-col class="mt-4">
        <b-progress max="25">
          <b-progress-bar :value="current_q_number+1"> {{current_q_number+1}}/25</b-progress-bar>
        </b-progress>
      </b-col>

      <b-col class="mt-4 text-center  " cols="12">
        <b-button class="h1 bg-transparent  border-0 shadow-none" :disabled="!buttons_disabled" @click="next() ">
          <b-icon icon="arrow-right-circle" class=" text-white" role="button"
                  style="width: 50px!important; height: 50px!important;"></b-icon>
        </b-button>
        <!--        modal-->
        <b-modal ref="my-modal" hide-header hide-footer>
          <div class="font-weight-bold">
            <b-icon icon="patch-check-fill" variant="success" scale="2" class="mr-3"></b-icon>{{answers.right}}
          </div>

          <div class="mt-3 font-weight-bold">
            <b-icon icon="patch-exclamation-fill" variant="danger" scale="2" class="mr-3"></b-icon>{{answers.wrong}}
          </div>

          <div class="text-right">
            <b-button variant="success" class="" onclick="location.reload()"> Təkrar</b-button>
          </div>
        </b-modal>


      </b-col>
    </b-row>


  </b-container>
</template>

<script>

    import {eventBus} from "@/main";

    export default {
        name: "welcome",
        data() {
            return {

                questions: [], //questions from json
                q_array: [],     //question bank
                unique_array: [],  //unique number array (25)
                current_question: "",
                variants: {
                    a: "",
                    b: "",
                    c: "",
                    d: "",
                    e: "",
                },
                current_correct_variant: '',
                current_q_number: 0,
                clicked_element: '',
                buttons_disabled: false,
                answers: {
                    right: 0,
                    wrong: 0
                }

            }
        },
        methods: {
            random_func(max) {
                return Math.floor(Math.random() * max);
            },
            next() {
                let buttons = document.querySelectorAll(".my-buttons")
                for (let i in buttons) {
                    if (!isNaN(i)) {
                        buttons[i].style.backgroundColor = '#F8F9FA'
                        buttons[i].style.color = 'black'
                    }
                }

                if (this.current_q_number < 24) {
                    this.current_q_number++;
                    this.current_question = this.q_array[this.current_q_number].question
                    this.variants.a = this.q_array[this.current_q_number].variants.a
                    this.variants.b = this.q_array[this.current_q_number].variants.b
                    this.variants.c = this.q_array[this.current_q_number].variants.c
                    this.variants.d = this.q_array[this.current_q_number].variants.d
                    this.variants.e = this.q_array[this.current_q_number].variants.e
                    this.current_correct_variant = this.q_array[this.current_q_number].correct
                    //    disabled buttons
                    this.buttons_disabled = false
                } else {
                    this.$refs['my-modal'].show()
                }
                console.log("True: " + this.answers.right, "False: " + this.answers.wrong)

            },
            create_method() {
                // console.log(this.questions.length)
                // emit questions number
                eventBus.$emit("number",this.questions.length)

                if (this.questions.length > 24) {
                    // console.log("Hello")
                    let unique = []
                    let isUnique = true
                    for (let i = 0; unique.length < 25; i++) {
                        isUnique = true

                        for (; isUnique;) {
                            let number = this.random_func(this.questions.length)
                            if (!unique.includes(number)) {
                                unique.push(number)
                                // console.log("Unique number: " + number)
                                isUnique = false
                                break
                            } else {
                                // unique.push(number)
                                // // console.log("Unique number: " + number)
                                // isUnique = false
                            }
                        }
                        // console.log("Question number: " + this.questions.length)
                        // console.log("Unique updated: " + unique + `(${unique.length})`)
                    }
                    this.unique_array = unique
                    console.log(this.unique_array)

                    //    update q_array

                    for (let item of this.unique_array) {
                        let number = parseInt(item)
                        // console.log(item)
                        this.q_array.push(this.questions[number])
                    }
                    this.current_question = this.q_array[0].question
                    this.variants.a = this.q_array[this.current_q_number].variants.a
                    this.variants.b = this.q_array[this.current_q_number].variants.b
                    this.variants.c = this.q_array[this.current_q_number].variants.c
                    this.variants.d = this.q_array[this.current_q_number].variants.d
                    this.variants.e = this.q_array[this.current_q_number].variants.e
                    this.current_correct_variant = this.q_array[this.current_q_number].correct


                }
            },
            state(event) {
                this.buttons_disabled = true
                this.clicked_element = event.currentTarget.id
                console.log(this.clicked_element, this.current_correct_variant)
                event.currentTarget.style.color = 'white'

                if (event.currentTarget.id == this.current_correct_variant) {
                    event.currentTarget.style.backgroundColor = 'green'
                    this.answers.right++
                } else {
                    event.currentTarget.style.backgroundColor = 'red'
                    document.getElementById(`${this.current_correct_variant}`).style.backgroundColor = 'green'
                    document.getElementById(`${this.current_correct_variant}`).style.color = 'white'

                    this.answers.wrong++
                }
            }
        },
        created() {
            this.$http.get("https://quiz-db-d2812-default-rtdb.firebaseio.com/data.json").then(response => {
                for (let item in response.data) {
                    this.questions.push(response.data[item]);
                }
                this.create_method()
            })

        }

    };
</script>

<style scoped>
</style>
