<template>
  <b-container>
    <b-row>
      <b-col cols="5" md="2" class="pr-0 ml-3">
        <b-form-input type="number" class="w-100" v-model="password"></b-form-input>
      </b-col>

      <b-col cols="4" md="1" class="pl-0 ml-1">
        <b-button variant="warning" size="md" @click="check" >Submit</b-button>
      </b-col>

      <b-col cols="1" md="1" class=" ml-auto"><h5 class=" text-light">{{number}}</h5>
      </b-col>
    </b-row>


    <b-container v-show="!disabled">


      <b-row class="mt-5 ">
        <b-form-group class="w-100" :disabled="disabled">
          <label for="textarea" class="text-white"> Question:</label>
          <b-textarea id="textarea" v-model="form_data.question"></b-textarea>

          <h4 class="d-inline text-white">A)</h4>
          <b-form-input class="mt-2 d-inline w-75" placeholder="Variant A"
                        v-model="form_data.variants.a"></b-form-input>
          <br>

          <h4 class="d-inline text-white">B)</h4>
          <b-form-input class="mt-2 d-inline w-75" placeholder="Variant B"
                        v-model="form_data.variants.b"></b-form-input>
          <br>

          <h4 class="d-inline text-white">C)</h4>
          <b-form-input class="mt-2 d-inline w-75" placeholder="Variant C"
                        v-model="form_data.variants.c"></b-form-input>
          <br>

          <h4 class="d-inline text-white">D)</h4>
          <b-form-input class="mt-2 d-inline w-75" placeholder="Variant D"
                        v-model="form_data.variants.d"></b-form-input>
          <br>

          <h4 class="d-inline text-white">E)</h4>
          <b-form-input class="mt-2 d-inline w-75" placeholder="Variant E"
                        v-model="form_data.variants.e"></b-form-input>
          <br>

          <b-form-group label="Correct variant:" class="text-white mt-3">
            <b-form-radio value="a" v-model="form_data.correct">A</b-form-radio>
            <b-form-radio value="b" v-model="form_data.correct">B</b-form-radio>
            <b-form-radio value="c" v-model="form_data.correct">C</b-form-radio>
            <b-form-radio value="d" v-model="form_data.correct">D</b-form-radio>
            <b-form-radio value="e" v-model="form_data.correct">E</b-form-radio>
          </b-form-group>

          <b-button variant="danger" block class="mb-2" @click="add()"> Submit</b-button>

        </b-form-group>


      </b-row>

    </b-container>

  </b-container>
</template>

<script>
    export default {
        name: "add",
        data() {
            return {
                password: null,
                selected: null,
                disabled: true,
                form_data: {
                    question: null,
                    variants: {
                        a: '',
                        b: '',
                        c: '',
                        d: '',
                        e: '',
                    },
                    correct: ''
                },
                number: 0
            }
        },
        methods: {
            check() {
                if (this.password == 553419) {
                    this.disabled = false
                }


            },
            add() {
                if (this.form_data.question===''||this.form_data.variants.a===''||this.form_data.variants.b===''||this.form_data.variants.c===''||this.form_data.variants.d===''||this.form_data.variants.e===''||this.form_data.correct===''){
                    alert("Empty form!")
                }else{
                    this.$http.post("https://quiz-db-d2812-default-rtdb.firebaseio.com/data.json", this.form_data).then(() => {
                        this.form_data = {
                            question: null,
                            variants: {
                                a: null,
                                b: null,
                                c: null,
                                d: null,
                                e: null,
                            },
                            correct: null
                        }
                    })
                }





                setTimeout(() => {
                    this.$http.get("https://quiz-db-d2812-default-rtdb.firebaseio.com/data.json").then(response => {
                        let num = 0
                        for (let i in response.data) {
                            num++
                        }
                        this.number = num
                    })
                }, 1000)

            },
        },
        created() {
            this.number = 0
            this.$http.get("https://quiz-db-d2812-default-rtdb.firebaseio.com/data.json").then(response => {
                let num = 0
                for (let i in response.data) {
                    num++
                }
                this.number = num
            })
        }
    }
</script>

